/* ----------------------- Global ----------------------- */
.pwb-clearfix:after {
	content: "";
	display: table;
	clear  : both;
}

.pwb-before-loop {
	margin-bottom: 1em;
}

.pwb-after-loop {
	margin-top: 1em;
}

/* ----------------------- /Global ----------------------- */

/* ----------------------- Carousels ----------------------- */
.pwb-carousel .pwb-slick-slide a {
	display: block;
}

.pwb-carousel .pwb-slick-slide a>img {
	margin: 0 auto;
}

.pwb-carousel .pwb-slick-slide,
.pwb-product-carousel .pwb-slick-slide {
	margin: 0 10px;
}

.pwb-carousel .slick-prev,
.pwb-carousel .slick-next,
.pwb-product-carousel .slick-prev,
.pwb-product-carousel .slick-next {
	font-size         : 0;
	position          : absolute;
	top               : 0;
	bottom            : 0;
	display           : block;
	cursor            : pointer;
	font-size         : 22px;
	width             : 30px;
	overflow          : hidden;
	text-align        : center;
	color             : #aeaeae;
	border            : none;
	outline           : none;
	z-index           : 999;
	background        : #eee;
	opacity           : 0;
	transition        : opacity 0.3s ease-in-out;
	-webkit-transition: opacity 0.3s ease-in-out;
	-moz-transition   : opacity 0.3s ease-in-out;
	-o-transition     : opacity 0.3s ease-in-out;
}

.pwb-carousel .slick-prev>span,
.pwb-carousel .slick-next>span,
.pwb-product-carousel .slick-prev>span,
.pwb-product-carousel .slick-next>span {
	display   : inline-block;
	height    : 20px;
	width     : 20px;
	padding   : 10px;
	position  : absolute;
	top       : 50%;
	margin-top: -20px;
	left      : 0;
	right     : 0;
}

.pwb-carousel .slick-prev:hover,
.pwb-carousel .slick-prev:focus,
.pwb-carousel .slick-next:hover,
.pwb-carousel .slick-next:focus,
.pwb-product-carousel .slick-prev:hover,
.pwb-product-carousel .slick-prev:focus,
.pwb-product-carousel .slick-next:hover,
.pwb-product-carousel .slick-next:focus {
	outline: none;
}

.pwb-carousel .slick-prev,
.pwb-product-carousel .slick-prev {
	left: 0;
}

.pwb-carousel .slick-next,
.pwb-product-carousel .slick-next {
	right: 0;
}

.pwb-carousel:hover .slick-next,
.pwb-carousel:hover .slick-prev,
.pwb-product-carousel:hover .slick-next,
.pwb-product-carousel:hover .slick-prev {
	opacity: 0.8;
}

.pwb-product-carousel .pwb-amount {
	display      : block;
	width        : 100%;
	margin-bottom: 12px;
}

/* ------ Preloader ------ */
.pwb-carousel,
.pwb-product-carousel {
	min-height: 30px;
}

.pwb-carousel .pwb-slick-slide,
.pwb-product-carousel .pwb-slick-slide {
	display: none;
}

.pwb-carousel.slick-initialized .pwb-slick-slide,
.pwb-product-carousel.slick-initialized .pwb-slick-slide {
	display: block;
}

.pwb-carousel .slick-list,
.pwb-product-carousel .slick-list {
	opacity : 0;
	position: absolute;
}

.pwb-carousel.slick-initialized,
.pwb-product-carousel.slick-initialized {
	min-height: none;
}

.pwb-carousel.slick-initialized .slick-list,
.pwb-product-carousel.slick-initialized .slick-list {
	opacity   : 1;
	position  : relative;
	transition: opacity 0.2s ease-in;
}

.pwb-carousel.slick-initialized .pwb-carousel-loader,
.pwb-product-carousel.slick-initialized .pwb-carousel-loader {
	display: none !important;
	opacity: 0 !important;
}

.pwb-carousel-loader {
	font-size        : 10px;
	margin           : 50px auto;
	text-indent      : -9999em;
	width            : 30px;
	height           : 30px;
	border-radius    : 50%;
	background       : #525252;
	background       : -moz-linear-gradient(left, #525252 10%, rgba(82, 82, 82, 0) 42%);
	background       : -webkit-linear-gradient(left, #525252 10%, rgba(82, 82, 82, 0) 42%);
	background       : -o-linear-gradient(left, #525252 10%, rgba(82, 82, 82, 0) 42%);
	background       : -ms-linear-gradient(left, #525252 10%, rgba(82, 82, 82, 0) 42%);
	background       : linear-gradient(to right, #525252 10%, rgba(82, 82, 82, 0) 42%);
	position         : relative;
	-webkit-animation: PWBCarouselLoader 1.4s infinite linear;
	animation        : PWBCarouselLoader 1.4s infinite linear;
	-webkit-transform: translateZ(0);
	-ms-transform    : translateZ(0);
	transform        : translateZ(0);
}

.pwb-carousel-loader:before {
	width        : 50%;
	height       : 50%;
	background   : #525252;
	border-radius: 100% 0 0 0;
	position     : absolute;
	top          : 0;
	left         : 0;
	content      : '';
}

.pwb-carousel-loader:after {
	background   : #ffffff;
	width        : 75%;
	height       : 75%;
	border-radius: 50%;
	content      : '';
	margin       : auto;
	position     : absolute;
	top          : 0;
	left         : 0;
	bottom       : 0;
	right        : 0;
}

@-webkit-keyframes PWBCarouselLoader {
	0% {
		-webkit-transform: rotate(0deg);
		transform        : rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		transform        : rotate(360deg);
	}
}

@keyframes PWBCarouselLoader {
	0% {
		-webkit-transform: rotate(0deg);
		transform        : rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		transform        : rotate(360deg);
	}
}

/* ------ /Preloader ------ */

/* ----------------------- /Carousels ----------------------- */

/* ----------------------- PWB All Brands ----------------------- */
.pwb-brands-cols-outer {
	margin-left : -15px;
	margin-right: -15px;
}

.pwb-all-brands .pwb-pagination-wrapper {
	margin-top : 20px;
	padding-top: 10px;
	border-top : 1px solid #ededed;
	text-align : right;
}

.pwb-all-brands .pwb-pagination {
	display         : inline-block;
	margin-right    : 10px;
	background-color: #ededed;
	color           : #333;
	text-align      : center;
	padding         : 10px;
}

.pwb-all-brands .pwb-brands-cols-outer:after {
	content: "";
	display: table;
	clear  : both;
}

.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3 {
	padding: 0 15px;
	width  : 20%;
	float  : left;
}

.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3>p:first-child {
	height   : 25px;
	overflow : hidden;
	font-size: 14px;
}

/* ----------------------- /PWB All Brands ----------------------- */

/* ----------------------- PWB AZ Listing ----------------------- */
.pwb-az-listing-row {
	margin-bottom: 20px;
}

.pwb-az-listing-row-in {
	margin-left : -5px;
	margin-right: -5px;
}

.pwb-az-listing-col {
	padding: 5px;
}

.pwb-az-listing-col>a {
	display       : block;
	text-transform: uppercase;
	white-space   : nowrap;
	overflow      : hidden;
}

.pwb-az-listing-title {
	border-bottom : 1px solid #ddd;
	font-weight   : bold;
	font-size     : 115%;
	text-transform: uppercase;
}

.pwb-az-listing-header {
	margin-bottom: 30px;
}

.pwb-az-listing-header ul {
	margin      : 0;
	padding     : 0;
	list-style  : none;
	margin-right: -2px;
	margin-left : -2px;
}

.pwb-az-listing-header li {
	display    : inline-block;
	float      : left;
	width      : 25px;
	height     : 25px;
	margin     : 2px;
	line-height: 25px;
	border     : 1px solid #ddd;
	text-align : center;
}

.pwb-az-listing-header a {
	text-transform: uppercase;
}

/* ----------------------- /PWB AZ Listing ----------------------- */

/* ----------------------- Filter products widget ----------------------- */
.pwb-filter-products input[type="checkbox"] {
	margin-right: 10px;
}

.pwb-filter-products li {
	margin-bottom: 5px;
}

/* ----------------------- /Filter products widget ----------------------- */

/* ----------------------- Columns ----------------------- */
.pwb-row:after {
	content: "";
	display: table;
	clear  : both;
}

.pwb-columns-2 {
	width: 50%;
}

.pwb-columns-3 {
	width: 33.33333%;
}

.pwb-columns-4 {
	width: 25%;
}

.pwb-columns-5 {
	width: 20%;
}

.pwb-columns-6 {
	width: 16.66666%;
}

.pwb-columns {
	float: left;
}

/* ----------------------- /Columns ----------------------- */

/* ----------------------- Single product ----------------------- */
.pwb-single-product-brands {
	padding-top: 1em;
	display    : flex;
	gap        : 0.1em;
}

.product_meta .pwb-single-product-brands {
	padding-top: 0;
}

.pwb-text-before-brands-links {
	margin-right: 4px;
}

/* ----------------------- /Single product ----------------------- */

/* ----------------------- Responsive ----------------------- */
@media screen and (max-width: 1000px) {
	.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3 {
		width: 25%;
	}
}

@media screen and (max-width: 700px) {
	.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3 {
		width: 50%;
	}

	.pwb-columns-5,
	.pwb-columns-6 {
		width: 25%;
	}
}

@media screen and (max-width: 480px) {
	.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3 {
		width: 100%;
		float: none;
	}

	.pwb-all-brands .pwb-brands-cols-outer .pwb-brands-col3>p:first-child {
		height: auto;
	}

	.pwb-columns-3,
	.pwb-columns-4,
	.pwb-columns-5,
	.pwb-columns-6 {
		width: 50%;
	}
}

@media (min-width: 576px) {
	.pwb-az-listing-col {
		width: 50%;
		float: left;
	}
}

@media (min-width: 992px) {
	.pwb-az-listing-col {
		width: 25%;
	}
}

/* ----------------------- /Responsive ----------------------- */